import { mapActions } from 'vuex'
import {setUserInfor,getUserInfor,removeUserInfor,removeId,getId,getIsShow,setIsShow,removeIsShow} from "../../util/index.js";
export default {
	data() {
		return {
			name:'',
			psd:'',
			checkPsd:'',
			isShow:false,
			userInfor:'',
			infor:''
		}
	},
	created() {
		getIsShow(this)
		getUserInfor(this)
	},
	onShow(){
		this.getUInfor()
	},
	methods: {
		...mapActions([
		            'handleLogin',
					'handleLoginOut'
		        ]),
		//去个人动态管理
		toDeleteActivity(){
			uni.reLaunch({
				url:'../deleteActivity/deleteActivity?userid='+this.userInfor._id
			})
		},
		//去个人信息修改
		toUpdateInfor(){
			uni.reLaunch({
				url:'../updateInfor/updateInfor?data='+encodeURIComponent(JSON.stringify(this.userInfor))
			})
		},
		//去我的喜欢
		toMyLove(){
			uni.reLaunch({
				url:'../myLove/myLove?userid='+this.userInfor._id
			})
		},
		//去我的下载
		toMyDownload(){
			uni.reLaunch({
				url:'../myDownload/myDownload?userid='+this.userInfor._id
			})
		},
		//去注册页
		toRegisterPage(){
			uni.reLaunch({
				url:'../register/register'
			})
		},
		//获取个人信息
		getUInfor(){
			uni.request({
				url: this.$baseURL+'/userInfor?_id='+this.userInfor._id,
				success: (res) => {
					this.userInfor=res.data[0]
				}
			})
		},
		getName(e){
			this.name=e.detail.value
		},
		getPsd(e){
			this.psd=e.detail
		},
		//登录
		login(){
			var name=this.name
			var psd=this.psd
			console.log("--123-->",this.name,this.psd)
			this.handleLogin({name,psd}).then(res=>{
				if(res[1].data.length>0){
					this.checkPsd=res[1].data[0].psd
					this.userInfor=res[1].data[0]
					if(this.psd==this.checkPsd){
						setIsShow(true)
						getIsShow(this)
						setUserInfor(this.userInfor)
						this.name=''
						this.psd=''
					}else{
						uni.showToast({
						    title: '密码有误',
							icon:'none',
						    duration: 2000
						});
						this.psd=''
					}
				}else{
					uni.showToast({
					    title: '信息填写有误',
						icon:'none',
					    duration: 2000
					});
					this.name=''
					this.psd=''
				}
			})
		},
		//退出登录
		loginOut(){
			setIsShow(false)
			getIsShow(this)
			setUserInfor('')
			getUserInfor(this)
		}
	}
}
